import { useWorker } from "../Utils";
export function isPrime(number) {
    return useWorker("isPrime", number);
}
export function getFactorsOf(number) {
    return useWorker("getFactorsOf", number);
}
export function getFactorTree(number) {
    return useWorker("getFactorTree", number);
}
function __gcd(x, y) {
    x = Math.abs(x);
    y = Math.abs(y);
    while (y) {
        var t = y;
        y = x % y;
        x = t;
    }
    return x;
}
function __lcm(x, y) {
    return Math.abs(x * y) / __gcd(x, y);
}
export function gcd(...numbers) {
    if (numbers.length === 0)
        throw "Empty argument for GCD!";
    let GCD = numbers[0];
    for (let i = 1; i < numbers.length; i++) {
        GCD = __gcd(GCD, numbers[i]);
    }
    return GCD;
}
export function lcm(...numbers) {
    if (numbers.length === 0)
        throw "Empty argument for LCM!";
    let LCM = numbers[0];
    for (let i = 1; i < numbers.length; i++) {
        LCM = __lcm(LCM, numbers[i]);
    }
    return LCM;
}
