export function factorial(from, to = 1) {
    let returned = 1;
    while (from > to) {
        returned *= from;
        from--;
    }
    return returned;
}
export function combination(n, r) {
    if (n - r < r)
        return factorial(n, r) / factorial(n - r);
    else
        return factorial(n, n - r) / factorial(r);
}
export function permutation(n, r) {
    return factorial(n, n - r);
}
export function repeatCombination(n, r) {
    return combination((n + r - 1), r);
}
export function repeatPermutation(n, r) {
    return n ** r;
}
export function stringPermutation(n, repeated) {
    let returned = factorial(n);
    if (returned === Infinity)
        return returned;
    let cache = {};
    for (let repeat of repeated) {
        if (repeat in cache) {
            returned /= cache[repeat];
            continue;
        }
        const fact = factorial(repeat);
        returned /= fact;
        cache[repeat] = fact;
    }
    return returned;
}
export const FORMULAS = [
    "n! / (r! * (n - r)!)",
    "n! / (n - r)!",
    "(r + n - 1)! / (r! * (n - 1)!)",
    "n^r"
];
export const OPERATIONS = [combination, permutation, repeatCombination, repeatPermutation];
