import { CalculatorSystem } from "./CalculatorSystem";
export class Calculator extends CalculatorSystem {
    constructor() {
        super({
            "add": (op1, op2) => op1 + op2,
            "sub": (op1, op2) => op1 - op2,
            "mult": (op1, op2) => op1 * op2,
            "div": (op1, op2) => op1 / op2,
            "mod": (op1, op2) => op1 % op2,
            "exp": (op1, op2) => op1 ** op2,
            "log": (op1, op2) => Math.log(op2) / Math.log(op1),
            "ln": (op) => Math.log(op),
            "sqrt": (op) => Math.sqrt(op),
            "inv": (op) => 1 / op,
            "fact": (op) => {
                let result = 1;
                while (op > 1 && result !== Infinity) {
                    result *= op;
                    op--;
                }
                return result;
            },
            "round": (op) => Math.round(op),
            "abs": (op) => Math.abs(op),
            "euler": (_) => Math.E,
            "pi": (_) => Math.PI
        });
    }
    getOperationQueue() {
        if (this.recentOperation() === "log") {
            return `log${this.recent()} (x)`;
        }
        return this.recent().toString() + ' ' + this.getOperationSymbol();
    }
    isInstantOperation(operationString) {
        return (operationString === "ln" ||
            operationString === "sqrt" ||
            operationString === "inv" ||
            operationString === "round" ||
            operationString === "fact" ||
            operationString === "euler" ||
            operationString === "pi");
    }
}
export class FractionCalculator extends CalculatorSystem {
    constructor() {
        super({
            "add": (op1, op2) => op1.add(op2),
            "sub": (op1, op2) => op1.sub(op2),
            "mult": (op1, op2) => op1.mult(op2),
            "div": (op1, op2) => op1.div(op2),
            "inv": (op) => op.flip(),
            "exp2": (op) => {
                return op.mult(op);
            }
        });
    }
    getOperationQueue() {
        return this.recent().toString() + ' ' + this.getOperationSymbol();
    }
    isInstantOperation(operationString) {
        return (operationString === "inv" || operationString === "exp2");
    }
}
